.sidebar{
    height: calc(100vh - 50px);
    flex: 1;
    position: sticky;
    background-color: rgb(251, 251, 255);
    top: 50px;
    box-shadow: rgba(0, 0, 0, 0.05) 0px 1px 2px 0px;
}
.sidebarWrapper{
    position: relative;
    padding: 20px;
    color: #555;
}
.sidebarMenu{
    margin-bottom: 30px;
}
.sidebar__title{
    font-size: 15px;
    color: rgb(181, 181, 186);
}
.sidebar__list{
    list-style: none;
    text-decoration: none;
    padding: 5px;
    font-size: 15px;
}
.sidebar__listitem{
    padding: 5px;
    cursor: pointer;
    display: flex;
    align-items: center;
    border-radius: 15px;
}
.sidebar__listitem.active , .sidebar__listitem:hover{
    background-color: rgb(240, 244, 255);
}
.logBtn{
    font-weight: bold;
    position: absolute;
    color: #fff;
    padding: 10px;
    border-radius: 25px;
    bottom: 0;
}
