.feature{
    width: 100%;
    display: flex;
    justify-content: space-evenly;
}
.feature__item{
    flex: 1;
    margin: 0 25px;
    padding: 30px;
    border-radius: 30px;
    cursor: pointer;
      box-shadow: rgba(0, 0, 0, 0.1) 0px 1px 3px 0px, rgba(0, 0, 0, 0.06) 0px 1px 2px 0px;
} 
.feature__title{
    font-size: 35px;
    font-family: sans-serif;
}
.feature__container{
    display: flex;
    align-items: center;
    margin: 10px 0px;
}
.feature__money{
    font-size: 25px;
    font-weight: 600px;
}
.feature__rate{
    display: flex;
    align-items: center;
    margin-left: 20px;
}
.featureicons{
    margin-left: 10px;
    color: green;
}
.featureicons.negative{
    color:red;
}