.topbar{
    width: 100%;
    height: 50px;
    background-color: #f9fcfa;
    position: sticky;
    top: 0;
    z-index: 999;
}
.topbar_wrapper{
    height: 100%;
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding: 0 20px;
}
.logo{
    font-weight: bold;
    font-family:serif;
    cursor: pointer;
    font-size: 35px;
    color: rgb(35, 35, 151);
}
.topbar__right{
    align-items: center;
    display: flex;
}
.topbar__icon{
    position: relative;
    cursor: pointer;
    margin-right: 20px;
    color: #555;
}
.bidgh{
    width: 15px;
    height: 15px;
    position: absolute;
    top: -5px;
    right: 0;
    background-color: red;
    color: #fff;
    display: flex;
    justify-content: center;
    align-items: center;
    font-size: 10px;
    border-radius: 50%;
}